.hobbies{
    padding: 20px;
    padding-left: 20px;
    padding-right: 20px;
    box-shadow: 0 8px 12px 0 rgba( 31, 38, 135, 0.37); 
}
.hobbies:hover{
    background-color: rgb(232, 240, 247);
}
.hobbies__line{
    padding-left: 20px;
    padding-right: 20px;
    margin-top: 100px;
    margin-bottom: 100px;
    
}

.hobbies__img{
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-around;
    flex-wrap: wrap;
    filter: opacity(0.4) drop-shadow(0 0 0#173e4e); 
    transition: transform .2s;
    width:50px;
    height: 50px;
  
    }
.hobbies__name{
    color: #173e4e;
    text-align: center;
}
.hobbies__img:hover{
    filter: opacity(0.4) drop-shadow(0 0 0#041319); 
    transform: scale(1.2);
}